<template>
  <v-container id="inventory" text-xs-center>
    <div>
      <p class="overline">100 (<span class="green"></span>) / 30  100(<span class="yellow"></span>) / 2  30 (<span class="red"></span>) / 1 (<span class="grey"></span>) /  (<span></span>)</p>
      <v-select
        :items="addressSel"
        v-model="addressSelChoice"
        label=""
      ></v-select>
      <div v-if="addressSelChoice == ''">
        <v-btn color="secondary" 
          dark 
          v-for="(adds, index) in address1"
          :key="index"
          @click="getStoreBy(adds)"
        >
          {{ adds.split(' ')[1] }}
        </v-btn>
      </div>
      <div v-if="addressSelChoice == ''">
        <v-btn color="secondary" 
          dark 
          v-for="(adds, index) in address2"
          :key="index"
          @click="getStoreBy(adds)"
        >
          {{ adds.split(' ')[1] }}
        </v-btn>
      </div>
    </div>
    <v-list subheader>
        <v-list-item
          v-for="(store, index) in stores"
          :key="index"
        >
          <v-list-item-content>
            <v-list-item-title>{{ store.name }} <span class="check" :class="getColor(store.remain_stat)"></span></v-list-item-title>
            <v-list-item-subtitle v-text="store.addr"></v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-icon @click="getGoogleMap(store.lat, store.lng)">
            <i class="material-icons">
              add_location
            </i>
          </v-list-item-icon>
        </v-list-item>
    </v-list>
  </v-container>
</template>

<script>
import axios from 'axios';
import { API_URL } from '^@/API';
import { globalEvent } from '^@/event';
export default {
  name: 'Sales',
  data: () => ({
    paging: {
      page:1,
      length: 5,
      totalCount: null
    },
    addressSel: ['', '', '', '', '', '', '', '', '', '','','','','','',''],
    addressSelChoice:'',
    // 
    address1: [' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' '],
    address2: [' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' ',' '],
    stores: null
  }),
  methods: {
    getStoreBy(add){
      globalEvent.$emit('updateLoader', true);
      const params = {
        address: add,
      }
      axios.get(API_URL+'/storesByAddr/json', {params: params})
        .then(res => {
          console.log(res);
          this.stores = res.data.stores;
          this.paging.page = parseInt(res.data.page);
          this.paging.totalCount = res.data.totalCount;
          this.paging.length = res.data.totalPages;
          globalEvent.$emit('updateLoader', false);
        })
    },
    getColor(color){
      if(color == 'plenty'){
        return 'green'
      }else if(color == 'some'){
        return 'yellow'
      }else if(color == 'few'){
        return 'red'
      }else if(color == 'empty'){
        return 'grey'
      }
    },
    getGoogleMap(lat, lng){
      window.open(`https://www.google.com/maps?q=${lat},${lng}&ll=${lat},${lng}&z=15`)
    }
  }
}
</script>

<style lang="scss" scoped>
  button {
    margin:2px;
  }
  .check {
    display:inline-block;
    width:10px;
    height:10px;
    margin-top:-4px;
    border-radius:50%;
    vertical-align: middle;
  }
</style>
